<template>
  <div class="hello">
  </div>
</template>

<script>

export default {
  name: 'postMessage',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      response: '',
      result: '',
      value: ''
    }
  },
  methods:{
    search: function(){
      console.log('cerated')
      var data = {
        s: this.value,
        limit: 10,
        type: 1
      }
      var qs = require('qs');
      this.$axios.post('api/search/pc',qs.stringify(data))
      .then(response=>{
        this.result=response.data.result.songs
        console.log(response.data.result)
      })
      .catch(error=>{
        console.log('error')
        console.log(error.data)
      })
    }
  } 
}
</script>

