export default {
	name: 'Header',
	methods: {
		openLeftPannel: function(){
			//this.$refs.asideLeft.open()
			this.$store.state.leftAsideIfShow=true
		}
	}
}
</script>
<style scoped>
.header{
	width: 100%;
