<template>
  <div id="accordionpanel" v-if="ifShow">
	<div v-for="line in list" id="line">
		<div id="icon"><n3-icon type="star" size="80px" color="red"></n3-icon></div>
		<div id="title">{{line.title}}</div>
		<div id="number">{{line.totalNumber}}首,已下载{{line.downloadNumber}}首</div>
	</div>
  </div>
</template>

<script>

export default {
  name: 'accordionPanel',
  //props:['ifShow'],
  props: {
  	ifShow: Boolean,
  },
  data () {
  	return {
  		list: [
  			{title: '我喜欢的音乐',totalNumber: 10,downloadNumber:4},
  			{title: '我喜欢的音乐',totalNumber: 10,downloadNumber:4},
  			{title: '我喜欢的音乐',totalNumber: 10,downloadNumber:4},
  			{title: '我喜欢的音乐',totalNumber: 10,downloadNumber:4},
  		]
  	}
  },
}
</script>

<style scoped>
#line{
	width: 100%;
	height: 140px;
}
#icon{
	float: left;
	margin-left: 3%;
	margin-right: 3%;
	margin-top: 20px;
}
#title{
	margin-top: 20px;
	display: inline-grid;
	font-size: 3rem;
}
#number{
	font-size: 2rem;
}
</style>



