<template>
	<div id="leftAside">
    <n3-aside  placement="left" title="Title" width="80%" ref="asideLeft" :header="false">
		<div id="background_image">
			<div id="userPhoto"></div>
			<div id="userName">聆竹</div>
		</div>
		<n3-card class="list">something</n3-card>
		<n3-card class="list">something</n3-card>
		<n3-card class="list">something</n3-card>
		<n3-card class="list">something</n3-card>
	</n3-aside>
	</div>
</template>
<script>
export default {
	name: 'leftAside',
	computed: {
		show: function(){
			return this.$store.state.leftAsideIfShow
		}
	},
	watch: {
		show: function(val,oldVal){
			if(val===true){
				this.$refs.asideLeft.open()
				this.$store.state.leftAsideIfShow = false	
			}
		}
	}
}
</script>
<style scoped>
#background_image{
	width: 105%;
	margin-top: -20px;
	margin-left: -20px;
	height: 400px;
	background-image: url('./../assets/yinse.png');
	//background:-webkit-gradient(linear, 0 0, 0 bottom, from(#f08080), to(#ffd700));  
}
#userPhoto{
	position: absolute;
	top: 100px;
	left: 40px;
	width: 200px;
	height: 200px;
	border-radius: 200px;
	background-color: #FFF;
	background-image: url(./../assets/userPhoto.png);
}
#userName{
	font-size: 3rem;
	position: absolute;
	top: 320px;
	left: 40px;
	width: 200px;
	//background-color: green;
	text-align: center;
}
.list{
	width: 105%;
	border: none;
	margin-left: -20px;
	padding: 30px 0 30px 40px;
	//padding-bottom: 20px;
	font-size: 3rem;
}
</style>


