<template>
	<div id="playPanel" >
		<div id="image"><n3-icon type="user" size="80px" color="red"></n3-icon></div>
		<div id="title" @click="showPlayDetail">{{title}}</div>
		<audio id="audio" :src="url" loop="true"></audio>
		
		<div id="playList"><n3-icon type="th-list" size="80px" color="red"></n3-icon></div>
		<div id="play" @click="playOrPause" ><n3-icon :type="play_button" size="80px" color="red"></n3-icon></div>	
	</div>
</template>
<script>
import url from './../assets/demo.mp3'
export default{
	name: 'playPanel',
	data () {
		return {
			title: '刚好遇见你',
			tips: '',
			url: url,
			play_button: 'play',
		}
	},
	methods: {
		playOrPause: function(){
			let play_control = document.getElementById('audio')
			if (play_control.paused===true) {
				play_control.play()
				this.play_button="pause"

			}
			else{
				play_control.pause()
				this.play_button="play"
			}
		},
		showPlayDetail () {
			//this.$refs.asideLeft.open()
			this.$store.state.ifShowDetail = true
		}
	}
}
</script>
<style scoped>
#playPanel{
	position: fixed;
	bottom: 0%;
	height: 120px;
	width: 100%;
	//background-color: red;
	//z-index: 1;
	border: solid 1px;
	background-color: #FFF;
}
#image{
	margin-top: 20px;
	margin-left: 2%;
	display: inline-grid;
	width: 80px;
	float: left;
}
#title{
	width: 55%;
	float: left;
	//background-color: red;
	margin-top: 20px;
	display: inline-grid;
	padding-left: 20px;
	font-size: 3rem;
}
#audio{
	
}
#playList{
	margin-top: 20px;
	margin-right: 3%;
	display: inline;
	float: right;
}
#play{
	margin-top: 20px;
	margin-right: 3%;
	display: inline;
	float: right;
}
</style>


