<template>
	<div id="detail">
		<n3-aside id="aside" placement="right"  width="100%" ref="right" :header="false">
		<div id="container">
			<div id="header">
				<div id="back" @click="closeAside"><n3-icon type="arrow-left" size="100px" color="white"></n3-icon></div>
				<div id="name">welcome</div>
				<div id="author">zhangsan</div>
			</div>
			<div id="center">
				<div id="center_image"></div>
			</div>
			<div id="progress">
			<mt-range
			  v-model="rangeValue"
			  :min="0"
			  :max="90"
			  :step="1"
			  :bar-height="5">
			</mt-range>
			</div>
			<div id="controls">
				<div id="backward"><n3-icon type="backward" size="80px"></n3-icon></div>
				<div id="play"><n3-icon type="play" size="80px"></n3-icon></div>
				<div id="forward"><n3-icon type="forward" size="80px"></n3-icon></div>
			</div>
		</div>
		</n3-aside>

	</div>
</template>
<script >
export default{
	name: 'playDetail',
	data () {
		return {
			rangeValue: 0			
		}

	},
	methods: {
		closeAside() {
			this.$refs.right.close()
		}
	},
	computed: {
		ifShowDetail: function(){
			return this.$store.state.ifShowDetail
		}
	},
	watch: {
		ifShowDetail: function(val,oldVal) {
			//console.log(val)
			if(val===true){
				this.$refs.right.open()
				this.$store.state.ifShowDetail = false	
			}
		}
	}
}
</script>
<style scoped>
#aside{
	background: -webkit-radial-gradient(left,rgba(205,150,205,0.4),	rgba(187,255,255,0.6),rgba(224,102,255,0.3));
}
#container{
	width: 102%;
	margin-top: -10px;
	margin-left: -1%;
}
#header{
	height: 200px;
	background-image: url('./../assets/yinse.png');
	background-size: 100%;
	opacity: 0.5;
	border: solid 1px black;
}

#back{
	width: 15%;
	float: left;
	padding-top: 50px;
}#name{
	width: 70%;
	//line-height: 200px;
	padding-top: 30px;
	font-size: 4rem;
}
#author{
	//background-color: green;
	width: 70%;
	font-size: 2rem;
}
#center{
	margin: auto;
	margin-top: 200px;
	width: 700px;
	height: 700px;
	border-radius: 500px;
	background-color: black;
}
#center_image{	
	position: relative;
	top: 150px;
	margin-left: 150px;
	width: 400px;
	height: 400px;
	border-radius: 400px;
	background-color: green;
}
#progress{
	position: fixed;
	bottom: 12%;
	margin: auto;
	width: 80%; 
	margin-left: 9%;
}
#controls{
	height: 100px;
	width: 100%;
	position: fixed;
	bottom: 0%;
	margin-bottom: 5%;
}
#backward{
	width: 10%;
	float: left;
	margin-left: 10%;
	//background-color: blue;
}
#play{
	width: 10%;
	margin-left: 25%;
	float: left;
	//background-color: red;
}
#forward{
	margin-right: 10%;
	float: right;
	width: 10%;
	//background-color: gray;
}
</style>


