<template>
  <div id="accordion">
  	<div id="title">
		<div id="icon" @click="change"><n3-icon type="angle-right" size="60px" color="gray" ></n3-icon></div>
		<div id="header" @click="change">{{title}}</div>
		<div id="setting"><n3-icon type="sun-o" size="60px" color="gray"></n3-icon></div>
	</div>
	<accordionPanel v-bind:ifShow="show"></accordionPanel>
  </div>
</template>

<script>
import accordionPanel from './accordionPanel'
export default {
  name: 'accordion',
  props: ['title'],
  data () {
  	return {
  		show: false,
  	}
  },
  components: {
  	accordionPanel: accordionPanel
  },
  methods: {
  	change: function(){
  		this.show = !this.show
  	}
  }
}
</script>

<style scoped>
#title{
	margin-top: 0px;
	width: 100%;
	height: 80px;
	background-color: #EDEDED;
}
#icon{
	padding-top: 10px;
	float: left;
	margin-left: 4%;

}
#header{
	width: 70%;
	display: inline-table;
	padding-top: 20px;
	font-size: 2rem;
	margin-left: 6%;
}
#setting{
	padding-top: 10px;
	padding-right: 8%;
	float: right;
}
</style>



